        /* Base Styles (same as projects page) */
        body {
            margin: 0;
            font-family: Arial, sans-serif;
            background-color: #F5F5DD;
        }

        .container {
            min-height: 100vh;
        }

        .links a {
            margin-left: 2rem;
            text-decoration: none;
            color: #333;
            font-weight: 500;
        }

        /* Contact Page Specific Styles */
        .contact-content {
            max-width: 800px;
            margin: 2rem auto;
            padding: 0 2rem;
        }

        .contact-section {
            background-color: #F1EAD2;
            border-radius: 15px;
            padding: 2rem;
            margin: 2rem 0;
            box-shadow: 0 4px 6px rgba(0,0,0,0.1);
            margin-top: 150px;
        }

        .social-media-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
            gap: 2rem;
            margin-top: 1.5rem;
        }

        .social-item {
            text-align: center;
            padding: 1rem;
        }

        .social-image {
            width: 80px;
            height: 80px;
            margin: 0 auto;
            border-radius: 50%;
            background-color: #ddd;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .social-image img {
            width: 100%;
            height: 100%;
            object-fit: contain; /* Ensures the whole image fits without distortion */
            border-radius: 50%; /* Ensures the image follows the container's shape */
        }

        .email-section {
            text-align: center;
            padding: 2rem;
            border-bottom: 2px solid #ddd;
            margin-bottom: 2rem;
        }

        .social-image :hover {
            transform: scale(1.2);
            cursor: pointer;
        }

        /* Responsive Design */
        @media (max-width: 768px) {
            .social-media-grid {
                grid-template-columns: 1fr;
            }
            
            .social-image {
                width: 60px;
                height: 60px;
            }
        }